// Section One
/* Section One */
.DyN {
    padding: 100px 20px;
    background-color: $primaryColor;
    position: relative;
    .rects {
        position: relative;
        margin-top: 50px;
        .rect-text {
            box-shadow: 0 0 20px 0px rgba($secondaryColor, .3);
            background-color: #fff;
            margin: 10px;
            transition: .3s;
            overflow: hidden;
            &:hover {
                box-shadow: 0 0 4px 4px rgba($secondaryColor, .3);
                img {
                    transform: scale(1.03)
                }
            }
            img {
                transition: .3s;
            }
            .text {
                padding: 35px 20px;
                @media #{$mediaExSmall} {
                    padding: 20px 20px;
                }
                @media #{$mediaSmall} {
                    padding: 22px 20px;
                }
                @media #{$mediaMedium} {
                    padding: 25px 20px;
                }
                @media #{$mediaLarge} {
                    padding: 30px 20px;
                }
                span {
                    display: block;
                    font-family: 'Junge', serif;
                    font-size: 23px;
                    line-height: 58px;
                    color: $brandColor;
                    width: 26px;
                    height: 48px;
                    border-bottom: 1px solid $brandColor;
                    @media #{$mediaExSmall} {
                        font-size: 16px;
                        line-height: 40px;
                        width: 16px;
                        height: 40px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 18px;
                        line-height: 44px;
                        width: 18px;
                        height: 42px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 20px;
                        line-height: 48px;
                        width: 20px;
                        height: 44px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 22px;
                        line-height: 53px;
                        width: 21px;
                        height: 45px;
                    }
                }
                p {
                    font-size: 14px;
                    line-height: 18px;
                    color: #000;
                    letter-spacing: .5px;
                    @media #{$mediaExSmall} {
                        font-size: 12px;
                        line-height: 16px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 12px;
                        line-height: 16px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 13px;
                        line-height: 17px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 13px;
                        line-height: 17px;
                    }
                }
            }
            .rect-img {
                padding: 0;
            }
        }
        .rect-2 {
            position: absolute;
            top: 0;
            right: 0;
            @media #{$mediaExSmall} {
                position: relative;
            }
            @media #{$mediaSmall} {
                position: relative;
                font-size: 12px;
                line-height: 16px;
            }
            @media #{$mediaMedium} {
                font-size: 13px;
                line-height: 17px;
            }
            @media #{$mediaLarge} {
                font-size: 13px;
                line-height: 17px;
            }
            .text {
                padding: 30px 20px;
            }
        }
    }
    .arrows {
        position: absolute;
        bottom: 0%;
        left: 48%;
        transform: translate(-50%, -20%);
        @media #{$mediaExSmall} {
            left: 45%;
        }
        @media #{$mediaSmall} {
            left: 48%;
        }
        @media #{$mediaMedium} {
            left: 48%;
        }
        @media #{$mediaLarge} {
            left: 48%;
        }
        li {
            width: 20px;
            list-style: none;
            display: inline-block;
            margin: 50px 10px 0;
            @media #{$mediaExSmall} {
                width: 35px;
                margin: 0px 25px 0;
            }
            @media #{$mediaSmall} {
                width: 35px;
                margin: 0px 40px 0;
            }
            @media #{$mediaMedium} {
                width: 30px;
            }
            @media #{$mediaLarge} {
                width: 25px;
            }
        }
    }
}

