        li {
            width: 20px;
            list-style: none;
            display: inline-block;
            margin: 10px;
            @media #{$mediaExSmall} {
                width: 35px;
                margin: 0px 25px 0;
            }
            @media #{$mediaSmall} {
                width: 35px;
                margin: 0px 40px 0;
            }
            @media #{$mediaMedium} {
                width: 30px;
            }
            @media #{$mediaLarge} {
                width: 25px;
            }
        }
    }
}

/* Section Three */
.OtY {
    padding: 100px 20px;
    background-color: $primaryColor;
    .OtY-content {
        box-shadow: 0 0 20px 0px rgba($secondaryColor, .3);
        background-color: #fff;
        text-align: center;
        height: 420px;
        width: 277px;
        margin: 20px 0;
        transition: .3s;
        overflow: hidden;
        &:hover {
            box-shadow: 0 0 4px 4px rgba($secondaryColor, .3);
            img {
                transform: scale(1.03)
            }
        }
        .text {
            position: relative;
            &:nth-last-child(odd) {
                &::after {
                    content: "";
                    width: 50%;
                    height: 70px;
                    background-color: #fff;
                    position: absolute;
                    top: -50px;
                    right: 0;
                    transform: skewY(10deg);
                }
                &::before {
                    content: "";
                    width: 50%;
                    height: 70px;
                    background-color: #fff;
                    position: absolute;
                    top: -50px;
                    left: 0;
                    transform: skewY(-10deg);
                }
            }
            &:nth-last-child(even) {
                &::after {
                    content: "";
                    width: 50%;
                    height: 80px;
                    background-color: #fff;
                    position: absolute;
                    top: 120px;
                    right: 0;
                    transform: skewY(-10deg);
                    z-index: 99;
                }
                &::before {
                    content: "";
                    width: 50%;
                    height: 80px;
                    background-color: #fff;
                    position: absolute;
                    top: 120px;
                    left: 0;
                    transform: skewY(10deg);
                    z-index: 99;
                }
            }
        }
        img {
            transition: .3s;
        }
        span {
            font-family: 'Junge';
            display: block;
            font-size: 20px;
            color: $brandColor;
            width: 50px;
            height: 50px;
            border-bottom: 1px solid $brandColor;
            margin: auto;
            padding-top: 20px;

        }
        p {
            font-size: 12px;
            line-height: 16px;
            color: $secondaryColor;
            padding: 10px 20px 30px;
            letter-spacing: .3px;
            margin-bottom: 0;
            margin-top: 0;
        }
    }
    .owl-dots {
        margin-top: 20px;
        width: 100%;
        height: 3px;
        .owl-dot {
            width: calc(100% / 8);
            height: 100%;
            background-color: rgba($secondaryColor, .2);
            &:first-child {
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
            }
            &:last-child {
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px;
            }
        }
        .active {
            background-color: $brandColor;
        }
    }
}

/* Section Four */
.sectors {
    height: 442.46px;
    h3 {
        @media #{$mediaExSmall} {
            top: 50px;
        }
        @media #{$mediaSmall} {
            top: 50px;
        }
        @media #{$mediaMedium} {
            top: 90px;
        }
        @media #{$mediaLarge} {
            top: 100px
        }
        &::after {
            @media #{$mediaSmall} {
                left: 33%;
            }
            @media #{$mediaMedium} {
                left: 35%;
            }
            @media #{$mediaLarge} {
                left: 38%;
            }
        }
        &::before {
            @media #{$mediaSmall} {
                right: 33%;
            }
            @media #{$mediaMedium} {
                right: 35%;
            }
            @media #{$mediaLarge} {
                right: 38%;
            }
        }
    }
    .slides {
        position: relative;
        .slider {
            .slider-list {
                padding: 0;
                margin: 0;
                position: relative;
                .sectors-content {
                    .text {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        text-align: center;
                        width: 500px;
                        overflow: hidden;
                        @media #{$mediaExSmall} {
                            width: 295px;
                        }
                        @media #{$mediaSmall} {
                            width: 310px;
                        }
                        @media #{$mediaMedium} {
                            width: 340px;
                        }
                        h4 {
                            font: {
                                family: 'Roboto', sans-serif;
                                size: 39px;
                                weight: 100;
                            }
                            line-height: 40px;
                            @media #{$mediaExSmall} {
                                font-size: 29px;
                                line-height: 35px;
                            }
                            @media #{$mediaSmall} {
                                font-size: 29px;
                                line-height: 35px;
                            }
                            @media #{$mediaMedium} {
                                font-size: 31px;
                                line-height: 38px;
                            }
                            @media #{$mediaLarge} {
                                font-size: 33px;
                                line-height: 39px;
                            }
                        }
                        p {
                            font: {
                                family: 'Roboto', sans-serif;
                                size: 14px;
                            }
                            line-height: 22px;
                            letter-spacing: 1px;
                            @media #{$mediaExSmall} {
                                font-size: 13px;
                                line-height: 18px;
                            }
                            @media #{$mediaSmall} {
                                font-size: 13px;
                                line-height: 18px;
                            }
                            @media #{$mediaMedium} {
                                font-size: 13px;
                                line-height: 20px;
                            }
                            @media #{$mediaLarge} {
                                font-size: 14px;
                                line-height: 22px;
                            }
                        }
                    }
                }
            }
        }
        .arrows {
            position: absolute;
            bottom: 20%;
            left: 48%;
            transform: translate(-50%, -20%);
            @media #{$mediaExSmall} {
                left: 45%;
                bottom: 15%;
            }
            @media #{$mediaSmall} {
                left: 48%;
                bottom: 15%;
            }
            @media #{$mediaMedium} {
                left: 48%;
            }
            @media #{$mediaLarge} {
                left: 48%;
            }
            li {
                width: 20px;
                list-style: none;
                display: inline-block;
                margin: 10px;
                @media #{$mediaExSmall} {
                    width: 35px;
                    margin: 0px 25px 0;
                }
                @media #{$mediaSmall} {
                    width: 35px;
                    margin: 0px 40px 0;
                }
                @media #{$mediaMedium} {
                    width: 30px;
                }
                @media #{$mediaLarge} {
                    width: 25px;
                }
            }
        }
    }
}

