/* Header */
.header {
    background-image: url("../images/main.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
    position: relative;
    margin: 0;
    padding: 0;
    .text-header {
        padding-top: 130px;
        display: flex;
        justify-content: center;
        h1 {
            font-family: 'Junge', serif;
            text-transform: uppercase;
            color: $brandColor;
            span {
                display: block;
                &:first-child {
                    position: relative;
                    font-size: 25px;
                    color: $secondaryColor;
                    margin-bottom: 15px;
                    line-height: 26px;
                    &::after {
                        content: "";
                        position: absolute;
                        top: 13px;
                        left: 55px;
                        width: 15%;
                        height: 1px;
                        background-color: $secondaryColor;
                        @media #{$mediaExSmall} {
                            top: 9px;
                            left: 45px;
                        }
                        @media #{$mediaSmall} {
                            top: 9px;
                            left: 45px;
                        }
                        @media #{$mediaMedium} {
                            top: 10px;
                            left: 45px;
                        }
                    }
                    &::before {
                        content: "";
                        position: absolute;
                        top: 13px;
                        right: 55px;
                        width: 15%;
                        height: 1px;
                        background-color: $secondaryColor;
                        @media #{$mediaExSmall} {
                            top: 9px;
                            right: 45px;
                        }
                        @media #{$mediaSmall} {
                            top: 9px;
                            right: 45px;
                        }
                        @media #{$mediaMedium} {
                            top: 10px;
                            right: 45px;
                        }
                    }
                    @media #{$mediaExSmall} {
                        font-size: 19px;
                        line-height: 20px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 19px;
                        line-height: 20px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 22px;
                        line-height: 23px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 25px;
                        line-height: 26px;
                    }
                }
                &:nth-child(2) {
                    font-size: 44px;
                    line-height: 25px;
                    @media #{$mediaExSmall} {
                        font-size: 35px;
                        line-height: 20px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 35px;
                        line-height: 20px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 39px;
                        line-height: 22px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 44px;
                        line-height: 25px;
                    }
                }
                &:last-child {
                    font-size: 25px;
                    margin-top: 10px;
                    line-height: 27px;
                    @media #{$mediaExSmall} {
                        font-size: 19px;
                        line-height: 21px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 19px;
                        line-height: 21px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 22px;
                        line-height: 24px;
                    }
                    @media #{$mediaLarge} {
                        font-size: 25px;
                        line-height: 27px;
                    }
                }
            }
        }
        button {
            font-family: 'Roboto', sans-serif;
            font-size: 13px;
            line-height: 23px;
            letter-spacing: 1px;
            text-transform: uppercase;
            background-color: $brandColor;
            border: none;
            color: $primaryColor;
            width: 286px;
            height: 72px;
            position: absolute;
            top: 90%;
            left: 50%;
            transform: translate(-50%, -90%);
            &:hover {
                img {
                    margin-left: 12px;
                }
            }
            img {
                width: 22px;
                height: 15px;
                margin-left: 8px;
                transition: .2s;
                @media #{$mediaExSmall} {
                    width: 21px;
                    height: 13px;
                }
                @media #{$mediaSmall} {
                    width: 21px;
                    height: 13px;
                }
                @media #{$mediaMedium} {
                    width: 21px;
                    height: 14px;
                }
                @media #{$mediaLarge} {
                    width: 22px;
                    height: 15px;
                }
            }
            &:focus {
                border: none;
                outline: none
            }
            @media #{$mediaExSmall} {
                font-size: 11px;
                line-height: 20px;
                width: 275.24px;
                height: 69.29px;
            }
            @media #{$mediaSmall} {
                font-size: 11px;
                line-height: 20px;
                width: 275.24px;
                height: 69.29px;
            }
            @media #{$mediaMedium} {
                font-size: 12px;
                line-height: 21px;
                width: 275.24px;
                height: 69.29px;
            }
            @media #{$mediaLarge} {
                font-size: 13px;
                line-height: 23px;
            }
        }
    }
}