/* Footer */
.footer {
    padding: 60px 20px;
    background-color: $mainColor;
    position: relative;
    &::after {
        content: "";
        width: 50%;
        height: 95px;
        background-color: #fff;
        position: absolute;
        top: -50px;
        right: 0;
        transform: skewY(6deg);
    }
    &::before {
        content: "";
        width: 50%;
        height: 95px;
        background-color: #fff;
        position: absolute;
        top: -50px;
        left: 0;
        transform: skewY(-6deg);
    }
    .barnd {
        width: 101px;
        height: 27px;
    }
    .navbar-list {
        text-align: center;
        margin-top: 30px;
        li {
            display: inline-block;
            font-size: 11px;
            font-weight: 100;
            text-transform: uppercase;
            padding: 0 10px;
            line-height: 23px;
            letter-spacing: 1px;
            position: relative;
            a {
                color: rgba($primaryColor, .75)
            }
            &::after {
                content: "";
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                width: 1px;
                height: 10px;
                background-color: rgba($primaryColor, .75)
            }
            &:last-child {
                &::after {
                    background-color: transparent
                }
            }
        }
    }
    .copyright {
        font-size: 11px;
        line-height: 23px;
        text-align: center;
        color: rgba($primaryColor, .5);
        letter-spacing: 1px;
        margin-top: 10px;
        @media #{$mediaExSmall} {
            line-height: 18px;
        }
        @media #{$mediaSmall} {
            line-height: 18px;
        }
        @media #{$mediaMedium} {
            line-height: 20px;
        }
    }
}