* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Body */
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Junge', serif;
}

p {
    font-family: 'Roboto', sans-serif;
}

/* Any H2 */
h2 {
    font-size: 27px;
    line-height: 68px;
    text-align: center;
    position: relative;
    text-transform: uppercase;
    &::after {
        content: "";
        position: absolute;
        top: 48%;
        left: 33%;
        width: 74px;
        height: 1px;
        background-color: $secondaryColor;
        @media #{$mediaExSmall} {
            left: 13%;
            width: 46px;
        }
        @media #{$mediaSmall} {
            left: 23%;
            width: 48px;
        }
        @media #{$mediaMedium} {
            left: 29%;
            width: 50px;
        }
        @media #{$mediaLarge} {
            left: 31%;
            width: 59px;
        }
    }
    &::before {
        content: "";
        position: absolute;
        top: 48%;
        right: 33%;
        width: 74px;
        height: 1px;
        background-color: $secondaryColor;
        @media #{$mediaExSmall} {
            right: 13%;
            width: 46px;
        }
        @media #{$mediaSmall} {
            right: 23%;
            width: 48px;
        }
        @media #{$mediaMedium} {
            right: 29%;
            width: 50px;
        }
        @media #{$mediaLarge} {
            right: 31%;
            width: 59px;
        }
    }
    @media #{$mediaExSmall} {
        font-size: 20px;
        line-height: 40px;
    }
    @media #{$mediaSmall} {
        font-size: 20px;
        line-height: 40px;
    }
    @media #{$mediaMedium} {
        font-size: 22px;
        line-height: 42px;
    }
    @media #{$mediaLarge} {
        font-size: 24px;
        line-height: 44px;
    }
}

/* Any H3 */
h3 {
    font-size: 18px;
    line-height: 31px;
    text-align: center;
    text-transform: uppercase;
    color: $primaryColor;
    letter-spacing: 1px;
    position: relative;
    top: 70px;
    &::after {
        content: "";
        position: absolute;
        top: 15px;
        left: 37%;
        width: 5%;
        height: 1px;
        background-color: $primaryColor;
        @media #{$mediaExSmall} {
            top: 13px;
            left: 27%;
        }
        @media #{$mediaSmall} {
            top: 13px;
            left: 27%;
        }
        @media #{$mediaMedium} {
            top: 14px;
            left: 29%;
        }
        @media #{$mediaLarge} {
            top: 15px;
            left: 30%;
        }
    }
    &::before {
        content: "";
        position: absolute;
        top: 15px;
        right: 37%;
        width: 5%;
        height: 1px;
        background-color: $primaryColor;
        @media #{$mediaExSmall} {
            top: 13px;
            right: 27%;
        }
        @media #{$mediaSmall} {
            top: 13px;
            right: 27%;
        }
        @media #{$mediaMedium} {
            top: 14px;
            right: 29%;
        }
        @media #{$mediaLarge} {
            top: 15px;
            right: 30%;
        }
    }
    @media #{$mediaExSmall} {
        font-size: 15px;
        line-height: 26px;
        top: 10px;
    }
    @media #{$mediaSmall} {
        font-size: 15px;
        line-height: 26px;
        top: 10px;
    }
    @media #{$mediaMedium} {
        font-size: 16px;
        line-height: 28px;
        top: 20px;
    }
    @media #{$mediaLarge} {
        font-size: 17px;
        line-height: 30px;
        top: 40px;
    }
}

/* Gloable Styles */
.uk-active {
    .bg {
        background: rgba($brandColor, .74);
        position: relative;
        z-index: 99;
        width: 100%;
        height: 100%;
        transition: .01s;
    }
}

.uk-cover-container {
    width: 100%;
    height: 100%;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(#000, .4);
        width: 100%;
        height: 100%;
    }
}