/* Navigation Bar */
.navbar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    font-family: 'Roboto', sans-serif;
    z-index: 99;
    .lang {
        font-size: 14px;
        margin-left: 30px;
        font-family: 'Cairo', sans-serif;
        color: $brandColor;
    }
    .navbar-brand {
        width: 100px;
    }
    .navbar-icon {
        width: 25px;
        height: 40px;
        margin-right: 30px;
        cursor: pointer;
    }
    .collapse {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $mainColor;
        display: none;
        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            background-color: transparent;
            z-index: -1;
            width: 95%;
            height: 95%;
            border: 1px solid $primaryColor;
            transform: translate(-50%, -50%);
            @media #{$mediaExSmall} {
                width: 90%;
                height: 90%;
            }
            @media #{$mediaSmall} {
                width: 90%;
                height: 90%;
            }
        }
        .col-top {
            position: absolute;
            color: #fff;
            width: 100%;
            top: 5%;
            @media #{$mediaExSmall} {
                top: 8%;
            }
            @media #{$mediaSmall} {
                top: 7%;
            }
            .icon {
                width: 20px;
                height: 20px;
                position: absolute;
                left: 10%;
                cursor: pointer;
                @media #{$mediaExSmall} {
                    left: 25%;
                    width: 18px;
                }
                @media #{$mediaSmall} {
                    left: 20%;
                }
                @media #{$mediaMedium} {
                    left: 15%;
                }
            }
            .search {
                position: absolute;
                right: 10%;
                font-size: 13px;
                text-transform: uppercase;
                letter-spacing: 1px;
                color: rgba(#fff, .75);
                cursor: pointer;
                @media #{$mediaExSmall} {
                    right: 25%;
                    font-size: 11px;
                    line-height: 27px;
                }
                @media #{$mediaSmall} {
                    right: 20%;
                    font-size: 11px;
                    line-height: 27px;
                }
                @media #{$mediaMedium} {
                    right: 15%;
                }
            }
        }
        .navbar-nav {
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            li.nav-item {
                position: relative;
                a.nav-link {
                    color: rgba(#fff, .75);
                    font-size: 15px;
                    text-transform: uppercase;
                    letter-spacing: 3px;
                    display: block;
                    padding: 15px;
                    @media #{$mediaExSmall} {
                        font-size: 11px;
                    }
                    @media #{$mediaSmall} {
                        font-size: 11px;
                    }
                    @media #{$mediaMedium} {
                        font-size: 13px;
                    }
                }
                &::after {
                    content: "";
                    position: absolute;
                    top: 25px;
                    width: 60px;
                    height: 1px;
                    @media #{$mediaExSmall} {
                        width: 30px;
                    }
                    @media #{$mediaSmall} {
                        width: 30px;
                    }
                    @media #{$mediaMedium} {
                        width: 40px;
                    }
                }
                &::before {
                    content: "";
                    position: absolute;
                    top: 25px;
                    width: 60px;
                    height: 1px;
                    @media #{$mediaExSmall} {
                        width: 30px;
                    }
                    @media #{$mediaSmall} {
                        width: 30px;
                    }
                    @media #{$mediaMedium} {
                        width: 40px;
                    }
                }
                &:hover {
                    &:nth-child(1), &:nth-child(2) {
                        &::after {
                            left: 16%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                left: 20%;
                                top: 23px;
                            }
                            @media #{$mediaSmall} {
                                left: 20%;
                                top: 23px;
                            }
                            @media #{$mediaMedium} {
                                left: 20%;
                            }
                        }
                        &::before {
                            right: 16%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                right: 20%;
                                top: 23px;
                            }
                            @media #{$mediaSmall} {
                                right: 20%;
                                top: 23px;
                            }
                            @media #{$mediaMedium} {
                                right: 20%;
                            }
                        }
                    }
                    &:nth-child(3), &:nth-child(4) {
                        &::after {
                            left: 6%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                left: 8%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                left: 10%;
                                top: 23px;
                            }
                            @media #{$mediaMedium} {
                                left: 10%;
                            }
                        }
                        &::before {
                            right: 6%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                right: 8%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                right: 10%;
                                top: 23px;
                            }
                            @media #{$mediaMedium} {
                                right: 10%;
                            }
                        }
                    }
                    &:nth-child(5) {
                        &::after {
                            left: -20%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                left: 0%;
                                top: 26px;
                            }
                            @media #{$mediaSmall} {
                                left: -12%;
                                top: 22px;
                            }
                        }
                        &::before {
                            right: -20%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                right: 0%;
                                top: 26px;
                            }
                            @media #{$mediaSmall} {
                                right: -12%;
                                top: 22px;
                            }
                        }
                    }
                    &:nth-child(6) {
                        &::after {
                            left: 9%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                left: 12%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                left: 12%;
                                top: 22px;
                            }
                        }
                        &::before {
                            right: 9%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                right: 12%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                right: 12%;
                                top: 22px;
                            }
                        }
                    }
                    &:nth-child(7) {
                        &::after {
                            left: 7%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                left: 10%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                left: 10%;
                                top: 22px;
                            }
                        }
                        &::before {
                            right: 7%;
                            background-color: rgba($primaryColor, .75);
                            @media #{$mediaExSmall} {
                                right: 12%;
                                top: 22px;
                            }
                            @media #{$mediaSmall} {
                                right: 12%;
                                top: 22px;
                            }
                        }
                    }
                }
            }
        }
    }
}